* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Lato", sans-serif;
}
.twitter{
  display: flex;
}
.sidebar{
  width:25%;
  position: fixed;
  left:0;
  height:100%;
border-right: 1px solid #e6ecf0
}

.sidebar ul {
  list-style-type:none;
}
.sidebar ul li a {
  display: flex;
  align-items: center;
  text-decoration:none;
  color:#000;
  padding: 18px 35px;
  font-weight:700;
  font-size:18px;
}
.sidebar ul li a:hover{
  background:#e8f5fe;
  color:#1da1f2
}

.icons{
  margin-right:15px;
  font-size:25px
}

.logo{
  color:#1da1f2
}
.sidebar_Btn{
  margin:15px 0;
}
.sidebar_Btn a{
  text-decoration:none;
  background:#1da1f2;
  color:#fff;
  padding:13px 35px;
  margin:0 35px;
  border-radius: 50px;
  outline:none;
  display:block;
  text-align:center

}

/* pots style */
.posts{
  width:50%;
  margin-left:25%;
  border-right:1px solid #e6ecf0;
}

.posts__home{
  width:50%;
  position:fixed;
  top:0;
  padding:15px;
  font-weight:700;
  border-bottom: 1px solid #e6ecf0;
  background:#fff;
  border-right:1px solid #e6ecf0

}

.create{
  padding-top:70px;
  border-bottom: 1px solid #e6ecf0;
  padding-bottom:20px;


}
.create__first{
  display: flex;
  padding:0 20px;
align-items:center;

}
.create__img{
  width:50px;
  height:50px;
  overflow:hidden;
  border-radius:50%;

}
.create__img img{
  widht:100%;
  height:100%
}

.create__input{
  margin-left:20px;

}

.create__control{
  border:none;
  outline:none;
  
}
.create__second{
  margin-left:20px;
  margin-top:15px;
display:flex;

}
.create__icons{
  width:50%;
}
.ic{
  margin : 0 6px;
  font-size:20px;
  cursor:pointer;
  color:#1da1f2

}

.create__btn{
  width:50%;
  text-align:right;
  margin-right:20px;

}
.create__btn a{
  text-decoration:none;
  background:#1da1f2;
  color:#fff;
  border-radius:50px;
  outline:none;
  padding: 10px 20px
}


/* posts styling */


.posts1{
  padding:20px;
border-bottom:1px solid #e6ecf0

}

.posts1__first{
  display:flex;
  align-items:center;
}

.posts1__first__img{
  width:50px;
  height:50px;
  overflow:hidden;
  border-radius:50%;
}

.posts1__first__img img{
  width:100%;
  height:100%;

}
.posts1__first__name{
  margin-left:6px;
}

.posts1__first__username{
  margin-left:6px;
  color:silver;
  font-weight:300

}
.posts1__first__username span{
  display: inline-block;
  margin-left:5px;
  font-size:14px;
}

.verify {
  margin-left:3px;
  color:#1da1f2
}


.posts1__details{
  margin:8px 30px;
  overflow: hidden;

}
.posts1__details__img{
  width:100%;
  margin-top:6px;

}
.posts1__details__img img{
  width:100%
}

.reactions{
  display: flex;
  justify-content: space-between;
  margin:8px 0;
  align-items:center;
  color:silver;
}

/* trends */
.trends{
  width:25%;
  
}

.trends__search{
  position:fixed;
  top:10px;
  /* border-bottom:1px solid #e6ecf0; */
  right:0;
  padding-bottom:20px;

  width:25%;
}
.trend__control{
  border:none;
  width:90%;
  margin-left:20px;
  outline:none;
background:#f5f4f2;
border-radius:50px;

padding:15px 40px;

}

.trend__icon{
  position: absolute;
  top:15px;
  left:40px;
}
.search{
  font-size:20px;
  color:silver
}


.keywords{
  position:fixed;
  top:80px;
  right:0;
  width:25%;
  padding:20px 20px;
}
.key{
  background:#f5f8fa;

padding:15px;
border-radius:5px;
}

.keywords__heading{
  font-size:20px;
  border-bottom:1px solid #e6ecf0;
  padding-bottom:8px

}
.country{
  font-size:14px;
  color:silver;
  font-weight:300;
  margin: 10px 0; 
}

.keywords__name{
  margin-bottom:8px;
}

.keywords__tweets{
  margin-top:7px;
  color:silver;
  font-size:14px;
  font-weight:300;
}